  .menu-toggle {
  position: absolute;
  top: 10px;
  right: 10px;
  display: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  color: white;
}

.menu-toggle.active .icon-menu path:nth-of-type(1) {
  transform: translateX(-10px);
 
}

.menu-toggle.active .icon-menu path:nth-of-type(2) {
  opacity: 0;
  
}

.menu-toggle.active .icon-menu path:nth-of-type(3) {
  transform: translateX(10px);
   
}

@media screen and (min-width: 300px) and (max-width:608px) {
  .menu-toggle {
    display: block;
    
   
  }

  .menu {
    position: fixed;
    top: 0;
    right: 0;
    width: 180px;
    height: 250px;
    background-color: #232323;
    border-bottom: 2px solid tomato;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
   
  }

  .menu.active {
    transform: translateX(0);
  }

  .menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
     color: white;
      display: flex;
    flex-direction: column;
  }

  .menu ul li {
    margin: 10px;
   
  }

  .menu ul li a {
    text-decoration: none;
    color: #333;
    font-size: 18px;
    transition: 0.25s;
  }

  .menu ul li a:hover{
    color: white;
  }

  body:hover{
    opacity: 0.5;
  }

  header.active ul{
    width: 30px;
  }

